.blog-page .single_post {
  -webkit-transition: all .4s ease;
  transition: all .4s ease
}

.blog-page .single_post .img-post {
  position: relative;
  overflow: hidden;
  max-height: 500px
}

.blog-page .single_post .img-post>img {
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  -webkit-transition: -webkit-transform .4s ease, opacity .4s ease;
  transition: transform .4s ease, opacity .4s ease;
  max-width: 100%;
  filter: none;
  -webkit-filter: grayscale(0);
  -webkit-transform: scale(1.01)
}

.blog-page .single_post .img-post:hover .social_share {
  display: block
}

.blog-page .single_post .img-post .social_share {
  position: absolute;
  bottom: 10px;
  left: 10px;
  display: none
}

.blog-page .single_post .img-post .social_share a.btn.btn-icon {
  line-height: 38px
}

.blog-page .single_post .meta {
  list-style: none;
  padding: 0;
  margin: 0
}

.blog-page .single_post .meta li {
  display: inline-block;
  margin-right: 15px
}

.blog-page .single_post .meta li a {
  font-style: italic;
  color: #959595;
  text-decoration: none;
  font-size: 12px
}

.blog-page .single_post .meta li a i {
  margin-right: 6px;
  font-size: 12px
}

.blog-page .single_post h3 {
  font-size: 20px;
  line-height: 26px;
  -webkit-transition: color .4s ease;
  transition: color .4s ease
}

.blog-page .single_post h3 a {
  color: #242424;
  text-decoration: none
}

.blog-page .single_post p {
  font-size: 15px
}

.blog-page .single_post .blockquote p {
  margin-top: 0 !important
}

.blog-page .right-box .popular-post ul li {
  margin-bottom: 15px
}

.blog-page .right-box .popular-post ul li:last-child {
  margin-bottom: 0
}

.blog-page .right-box .popular-post ul li h5 {
  font-size: 18px
}

.blog-page .right-box .popular-post ul li h5 a {
  color: #333
}

.blog-page .right-box .popular-post ul li .author-name,.blog-page .right-box .popular-post ul li .date {
  display: block
}

.blog-page .right-box .tag-clouds ul li {
  display: inline-block
}

.blog-page .right-box .instagram-plugin {
  overflow: hidden
}

.blog-page .right-box .instagram-plugin li {
  float: left;
  overflow: hidden;
  width: 24.89%;
  border: 1px solid #fff
}

.blog-page .comment-reply li {
  margin-bottom: 10px
}

.blog-page .comment-reply li:last-child {
  margin-bottom: 0;
}

.blog-page .comment-reply li h5 {
  font-size: 17px
}

.blog-page .comment-reply li p {
  margin-bottom: 0;
  font-size: 15px;
  color: #777
}

.blog-page .comment-reply .list-inline li {
  display: inline-block;
  margin: 0;
  padding-right: 20px
}

.blog-page .comment-reply .list-inline li a {
  font-size: 13px
}

.page.with-sidebar.right .left-box {
  margin-right: -20px
}


.single_post pre {
  background: #eee;
}

.single_post pre code {
  line-height: 20px;
  padding: 20px;
  display: block;
}

@media (max-width: 414px) {
  .section.blog-page {
    padding:20px 0
  }

  .blog-page .left-box .single-comment-box>ul>li {
    padding: 25px 0
  }

  .blog-page .left-box .single-comment-box ul li .icon-box {
    display: inline-block
  }

  .blog-page .left-box .single-comment-box ul li .text-box {
    display: block;
    padding-left: 0;
    margin-top: 10px
  }
}
