.cbp_tmtimeline {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative; }

.cbp_tmtimeline:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 3px;
  background: #eee;
  left: 20%;
  margin-left: -6px; }

.cbp_tmtimeline > li {
  position: relative; }

.cbp_tmtimeline > li:first-child .cbp_tmtime span.large {
  color: #444;
  font-size: 17px !important;
  font-weight: 700; }

.cbp_tmtimeline > li:first-child .cbp_tmicon {
  background: #fff;
  color: #666; }

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmtime span:last-child {
  color: #444;
  font-size: 13px; }

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel {
  background: #f0f1f3; }

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel:after {
  border-right-color: #f0f1f3; }

.cbp_tmtimeline > li .empty span {
  color: #777; }

.cbp_tmtimeline > li .cbp_tmtime {
  display: block;
  width: 23%;
  padding-right: 70px;
  position: absolute; }

.cbp_tmtimeline > li .cbp_tmtime span {
  display: block;
  text-align: right; }

.cbp_tmtimeline > li .cbp_tmtime span:first-child {
  font-size: 15px;
  color: #3d4c5a;
  font-weight: 700; }

.cbp_tmtimeline > li .cbp_tmtime span:last-child {
  font-size: 14px;
  color: #444; }

.cbp_tmtimeline > li .cbp_tmlabel {
  margin: 0 0 15px 25%;
  background: #f0f1f3;
  padding: 1.2em;
  position: relative;
  border-radius: 5px; }

.cbp_tmtimeline > li .cbp_tmlabel:after {
  right: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-right-color: #f0f1f3;
  border-width: 10px;
  top: 10px; }

